import React from 'react'
import PropTypes from 'prop-types'

const Footer = (props) => (
    <footer id="footer" style={props.timeout ? {display: 'none'} : {}}>
        <p className="copyright">&copy;Universidade Estadual do Paraná - Campus de União da Vitória.</p>
        <p className="copyright">Praça Coronel Amazonas, s/nº - Caixa Postal 57</p>
        <p className="copyright">União da Vitória - Paraná - Brasil - CEP 84600-000</p>
        <p className="copyright">Fone/Fax: (42) 3521-9100</p>
    </footer>
)

Footer.propTypes = {
    timeout: PropTypes.bool
}

export default Footer
