import PropTypes from 'prop-types'
import React from 'react'
import pic03 from '../images/2logo_clube.png'

const Header = props => (
  <header id="header" style={props.timeout ? { display: 'none' } : {}}>
    <div className="logo">
      <span className="icon fa-flask"></span>
    </div>
    <div className="content">
      <div className="inner">        
      
      <span className=" main fit">
      <img style={{ height: 'auto', width: '100%' }} src={pic03} alt="" />
    </span>
            <p>          
        </p>
      </div>
    </div>
    <nav>
      <ul>
        <li>
          <button
            onClick={() => {
              props.onOpenArticle('intro')
            }}
          >
            Sobre
          </button>
        </li>
        <li>
          <button
            onClick={() => {
              props.onOpenArticle('about')
            }}
          >
          Informações
          </button>
        </li>
        <li>
          <button
            onClick={() => {
              props.onOpenArticle('contact')
            }}
          >
            Inscrição Instituição
          </button>
        </li>
        <li>
        <button style={{ 'background-color': '#F08080' }}
          onClick={() => {
            props.onOpenArticle('work')
          }}
        >
        Inscrição “Mulheres na Ciência”
        </button>
      </li>
      <li>
          <button
          >
       <a href="https://drive.google.com/open?id=12-uFffU3L5MQ3X4pCCCOHwq08wat7ERk" download="termo">Download</a>
          </button>
        </li>
      </ul>
    </nav>
  </header>
)

Header.propTypes = {
  onOpenArticle: PropTypes.func,
  timeout: PropTypes.bool,
}

export default Header
