import PropTypes from 'prop-types'
import React from 'react'

import pic01 from '../images/lab-ciencias.jpg'
import pic02 from '../images/mulher-ciencias.jpg'
import pic03 from '../images/inf-ciencias.jpg'



class Main extends React.Component {
  render() {
    let close = (
      <div
        className="close"
        onClick={() => {
          this.props.onCloseArticle()
        }}
      ></div>
    )

    return (
      <div
        ref={this.props.setWrapperRef}
        id="main"
        style={this.props.timeout ? { display: 'flex' } : { display: 'none' }}
      >
        <article
          id="intro"
          className={`${this.props.article === 'intro' ? 'active' : ''} ${
            this.props.articleTimeout ? 'timeout' : ''
          }`}
          style={{ display: 'none' }}
        >
          <h2 className="major">Sobre o projeto</h2>
          <span className="image main">
            <img src={pic01} alt="" />
          </span>
          <p>
          O Clube de Ciências é um projeto de extensão universitária que oferece mentorias gratuitas através de encontros quinzenais nas quartas-feiras para a realização de experimentos com alunos em idade escolar. Serão promovidas atividades diversificadas com experimentos, diálogos sobre Ecotox e discussões dirigidas relacionadas a um tema de interesse científico dentro das áreas de biologia, química e física.
          </p>
          <p>
          As instituições interessadas deverão inscrever suas turmas de 15/03/20 a 29/03/20 no item “Inscrições”. Poderão ser turmas mistas de alunos do Ensino Fundamental II e Ensino Médio com no máximo 25 alunos. Após o preenchimento da inscrição, entraremos em contato via e-mail para combinar os dias dos encontros. Após confirmada a inscrição, a instituição deverá providenciar o termo de autorização devidamente preenchido e entregue no primeiro dia do encontro. O termo está disponível em “Downloads”.
          </p>
          {close}
        </article>

        <article
          id="work"
          className={`${this.props.article === 'work' ? 'active' : ''} ${
            this.props.articleTimeout ? 'timeout' : ''
          }`}
          style={{ display: 'none' }}
        >
          <h2 className="major">Edição “Mulheres na Ciência”</h2>
          <span className="image main">
            <img src={pic02} alt="" />
          </span>
          <p>
          O Clube de Ciências UNESPAR vai proporcionar uma edição especial com o tema “Mulheres na Ciência”. Serão disponibilizadas 20 vagas para meninas em idade escolar do Ensino Fundamental II e Ensino Médio para encontros mensais nas quintas-feiras com o intuito de incluir cada vez mais meninas e mulheres na Ciência. Além de encorajar as novas gerações a buscarem e se dedicarem à carreira de cientista.
          </p>
          <p>
          Meninas interessadas deverão preencher o formulário no item “Inscrições”. A inscrição será confirmada via e-mail e a data dos encontros serão publicadas no site. No primeiro dia do encontro, as inscritas deverão levar o termo de autorização (disponível em downloads) devidamente preenchido e assinado.            
          </p>        
          <h2 className="major">Formulário “Mulheres na Ciência”</h2>
          <form method="post" action="https://getform.io/f/d1d1610d-3c8a-45fc-8d3f-c41c370fa90a">
            <div className="field first">
              <label htmlFor="name">Nome</label>
              <input type="text"  name="nome_aluna" id="nome_aluna" placeholder="Nome da aluna" required/>
            </div>
            <div className="field half">
              <label htmlFor="name">Idade</label>
              <input defaultValue="1" type="number" name="idade" id="idade" placeholder="Idade da aluna " min="1" max="99" required/>
            </div>
          <div className="field half">
          <label for="ano-escola">Ano da escola</label>
          <select id="ano-escola" name="ano-escola" required>
            <option value="sexto-ano">6º Ano</option>
            <option value="setimo-ano">7º Ano</option>
            <option value="oitavo-ano">8º Ano</option>            
            <option value="nono-ano">9º Ano</option>            
            <option value="ensino-medio">Ensino Médio</option>                                   
          </select>
          </div>
          <div className="field ">
          <label htmlFor="cidade">cidade </label>
          <input type="text" name="cidade" id="cidade" placeholder="Nome da cidade" placeholder="Cidade da instituição" required/>
        </div>
        <div className="field ">
              <label htmlFor="endereço">Endereço</label>
              <input type="text" name="endereço" required id="endereço" placeholder="Endereço da insituição" required/>
        </div>
        <div className="field half">
              <label htmlFor="telefone">Telefone</label>
              <input type="text" name="idatelefonede" id="telefone" placeholder="Telefone" />
        </div>
        <div className="field half">
              <label htmlFor="name">E-mail</label>
              <input type="email" name="email" id="email" placeholder="Endereço de e-mail" required/>
        </div>
        <div className="field ">
              <label htmlFor="responsavel">Responsável</label>
              <input type="text" name="responsavel" id="responsavel" placeholder="Nome do responsável" required/>
        </div>
        <div>
            
            <label htmlFor="encontros">Interesse:</label>
            <input type="radio" id="mensais" name="encontros" value="mensais" defaultChecked="true" />
            <label for="quinzenais">Encontros mensais</label>

        </div>

            <div>
            </div>
            <ul className="actions">
              <li>
                <input type="submit" value="Realizar inscrição" className="special" />
              </li>
              <li>
                <input type="reset" value="Limpar" />
              </li>
            </ul>
          </form>
          <ul className="icons">
            <li>
              <a href="https://www.instagram.com/clube_ciencias/" className="icon fa-instagram">
                <span className="label">Instagram</span>
              </a>
            </li>
          </ul>
          {close}




        </article>

        <article
          id="about"
          className={`${this.props.article === 'about' ? 'active' : ''} ${
            this.props.articleTimeout ? 'timeout' : ''
          }`}
          style={{ display: 'none' }}>

          <h2 className="major">Informações</h2>
          <span className="image main">
            <img src={pic03} alt="" />
          </span>
          
          <p>Para maiores informações ou dúvidas entrar em contato via:</p>
          <p>E-mail:  <a href="mailto:clubedecienciasunespar@gmail.com">clubedecienciasunespar@gmail.com</a></p>
          <p> Telefone: (42) 99960-0313</p>
          <p>Profª Drª Patrícia Barbosa<br />
          Professora do colegiado de Ciências Biológicas <br />
          UNESPAR – campus de União da Vitória – PR</p>
          
          
          {close}
        </article>

        <article
          id="contact"
          className={`${this.props.article === 'contact' ? 'active' : ''} ${
            this.props.articleTimeout ? 'timeout' : ''
          }`}
          style={{ display: 'none' }}
        >
          <h2 className="major">Formulário para Instituições</h2>
          <form method="post" action="https://getform.io/f/abb9f850-b4e7-494c-b929-459bc3cce24f">
            <div className="field first">
              <label htmlFor="name">Nome insituição</label>
              <input type="text"  name="nome_instituicao" id="nome_instituicao" placeholder="Nome da instituição" required/>
            </div>
            <div className="field half">
              <label htmlFor="name">Número de alunos</label>
              <input defaultValue="1" type="number" name="numero_alunos" id="numero_alunos" placeholder="Número de alunos " min="1" max="25" required/>
            </div>
          <div className="field half">
          <label for="faixa-etaria">Faixa etária</label>
          <select id="faixa-etaria" name="faixa-etaria" required>
            <option value="onze-quatorze">11 a 14</option>
            <option value="quinze-dezoito">15 a 18</option>
            <option value="misto" selected>Misto</option>            
          </select>
          </div>
          <div className="field ">
          <label htmlFor="cidade">cidade </label>
          <input type="text" name="cidade" id="cidade" placeholder="Nome da cidade" placeholder="Cidade da instituição" required/>
        </div>
        <div className="field ">
              <label htmlFor="endereço">Endereço</label>
              <input type="text" name="endereço" required id="endereço" placeholder="Endereço da insituição" required/>
        </div>
        <div className="field ">
              <label htmlFor="professor">Professor responsável</label>
              <input type="text" name="professor" id="professor" placeholder="Professor responsável" required/>
        </div>
        <div>
            
            <label htmlFor="encontros">Interesse:</label>
            <input type="radio" id="quinzenais" name="encontros" value="quinzenais " defaultChecked="true" />
            <label for="quinzenais">Encontros quinzenais</label>
            <input type="radio" id="visita" name="encontros" value="visita"  />
            <label for="visita">Visita</label>

        </div>

            <div>
            </div>
            <ul className="actions">
              <li>
                <input type="submit" value="Realizar inscrição" className="special" />
              </li>
              <li>
                <input type="reset" value="Limpar" />
              </li>
            </ul>
          </form>
          <ul className="icons">
            <li>
              <a href="https://www.instagram.com/clube_ciencias/" className="icon fa-instagram">
                <span className="label">Instagram</span>
              </a>
            </li>
          </ul>
          {close}
        </article>
      </div>
    )
  }
}

Main.propTypes = {
  route: PropTypes.object,
  article: PropTypes.string,
  articleTimeout: PropTypes.bool,
  onCloseArticle: PropTypes.func,
  timeout: PropTypes.bool,
  setWrapperRef: PropTypes.func.isRequired,
}

export default Main
